* {
   box-sizing: border-box;
}

html {
   font-family: Arial, Helvetica, sans-serif;
   line-height: 1.6;
}

body {
   font-size: rem(18px);
   margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
   margin: 0;
}

section,
main,
footer {
   padding: 0;
}

img {
   width: 100%;
}

p {
   margin: 0;
}

a {
   text-decoration: none;
}
